div#portfolio {
  overflow: hidden;
}
.portfolio-nav {
  position: absolute;
  font-size: 30px;
  top: 48%;
}
.portfolio-nav.left {
  left: 40px;
}
.portfolio-nav.right {
  right: 36px;
}
.projects {
  margin: 40px 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  min-height: 260px;
}
.project {
  width: 100%;
  min-height: 250px;
  text-align: center;
}
.project-image {
  min-height: 200px;
}
.project-image img {
  border-radius: 50%;
  width: 50%;
}
.project-info p {
  font-size: 12px;
}
@media screen and (max-width: 540px) {
  .portfolio-nav {
    font-size: 40px;
    top: 42%;
  }
  .project {
    min-height: 200px;
  }
  .project-image {
    min-height: 140px;
  }
  .project-image img {
    width: 60%;
  }
  .project-info h2 {
    font-size: 14px !important;
    margin-bottom: 5px;
  }
  .portfolio-nav i.icon {
    font-size: 0.6em;
  }
}
